<template>
  <div class="card card-body mt-4">
    <h1>Shopping list</h1>
    <p>
      Enter the name of the item in the textbar and press "Add item" to put it
      in the shopping list.
    </p>
    <form @submit.prevent="onSubmit">
      <div class="form-group">
        <label>Föremål</label>
        <input v-model="form.item" class="form-control" type="text" required />
      </div>

      <button type="submit" class="btn btn-success mt-3">Add item</button>
    </form>
  </div>
</template>

<script>
import { createItem } from "@/firebase";
import { reactive } from "vue";

export default {
  setup() {
    const form = reactive({ item: "" });

    const onSubmit = async () => {
      await createItem({ ...form });
      form.item = "";
    };

    return { form, onSubmit };
  },
};
</script>

