<template>
  <div class="card mt-4">
    <div class="card-body">
        <h5 class="card-title">Shopping list</h5>
        <p class="card-text">
          This is the items in your shopping list
        </p>
      </div>
    <table class="table m-o">
      <thead>
        <tr>
          <th scope="col">Item name</th>
          <th scope="col">Action</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="{ id, item } in items" :key="id">
          <td>{{ item }}</td>
          <td>
            <button
              class="btn btn-primary btn-sm me-2"
              @click="addToQueuedList(id, item)"
            >
              Added to cart!
            </button>
            <button class="btn btn-danger btn-sm" @click="deleteItem(id)">
              Delete
            </button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { useLoadItem, deleteItem, createQueued } from "@/firebase";
import { reactive } from "vue";

export default {
  setup() {
    const items = useLoadItem();

    const addToQueuedList = async (id, item) => {
      const form = reactive({ item: item });
      deleteItem(id);
      await createQueued({ ...form });
    };

    return { items, deleteItem, addToQueuedList };
  },
};
</script>

