<template>
  <div>
    <div class="card mt-4">
      <div class="card-body">
        <h5 class="card-title">Cart</h5>
        <p class="card-text">
          This is the curremt items in your shopping cart
        </p>
      </div>
      <table class="table m-o">
        <thead>
          <tr>
            <th scope="col">Item name</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="{ id, item } in items" :key="id">
            <td>{{ item }}</td>
          </tr>
        </tbody>
      </table>
    </div>
    <button class="btn btn-danger btn-sm mt-4" @click="deleteItems(items)">
      Clear entire shopping cart
    </button>
  </div>
</template>

<script>
import { deleteQueued, useLoadQueued } from "@/firebase";

export default {
  setup() {
    const items = useLoadQueued();

    const deleteItems = async (items) => {
      const deletePromises = items.map((item) => deleteQueued(item.id));
      await Promise.all(deletePromises);
    };

    return { items, deleteItems };
  },
};
</script>

